.App {
  text-align: center;
  margin-top: 20vh;
  font-family: 'Courier New', Courier, monospace;
  font-size: 2.5rem;
  color: #333;
}


.cursor {
  display: inline-block;
  width: 2px;
  height: 1em;
  background-color: black;
  margin-left: 2px;
  animation: blink 1s step-end infinite;
}

@keyframes blink {
  from, to { background-color: black; }
  50% { background-color: transparent; }
}

